export class Life {
  private lifeScore: number;
  private lifeDisplay: HTMLElement | null;

  public constructor() {
    this.lifeScore = 3;
    this.lifeDisplay = document.querySelector('#life');
    this.showLife();
  }

  public getLifeScore(): number {
    return this.lifeScore;
  }

  decreaseLife(): void {
    this.lifeScore -= 1;
    this.showLife();
  }

  showLife(): void {
    if (this.lifeDisplay)
      this.lifeDisplay.innerHTML = 'life: ' + this.lifeScore.toString();
  }
}
