export class Score {
  private score: number;
  private scoreDisplay: HTMLElement | null;

  public constructor() {
    this.score = 0;
    this.scoreDisplay = document.querySelector('#score');
    this.showScore();
  }

  addScore() {
    this.score += 10;
    this.showScore();
  }
  showScore() {
    if (this.scoreDisplay)
      this.scoreDisplay.innerHTML = 'score: ' + this.score.toString();
  }
}
