import { Airplane } from '../data/Airplane';
import { Boat } from '../data/Boat';
import { Life } from '../data/Life';
import { Parachutist } from '../data/Parachutist';
import { Score } from '../data/Score';
import { Vector } from '../types';
import { Sea } from '../data/Sea';

import PARACHTIST_IMG from '../assets/parachutist.png';
import SEA_IMG from '../assets/sea.png';

export class CanvasView {
  private canvas: HTMLCanvasElement;
  private context: CanvasRenderingContext2D | null;
  private score: Score;
  private life: Life;
  private info: HTMLElement | null;
  private boat: Boat;
  private airplane: Airplane;
  private sea: Sea;
  private parachutistList: any;

  public constructor(canvasName: string, airplane: Airplane, boat: Boat) {
    this.canvas = document.querySelector(canvasName) as HTMLCanvasElement;
    this.context = this.canvas.getContext('2d');
    this.score = new Score();
    this.life = new Life();
    this.info = document.querySelector('#info');
    this.boat = boat;
    this.airplane = airplane;
    const seaVector: Vector = { x: 0, y: 550 };
    this.sea = new Sea(1080, 100, seaVector, SEA_IMG);
    this.parachutistList = new Set<Parachutist>();
  }

  clear(): void {
    this.context?.clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  start(): void {
    this.score.showScore();
    this.life.showLife();
    this.gameLoop();
  }

  drawScore(): void {
    this.score.addScore();
  }
  drawLife(): void {
    if (this.life.getLifeScore() > 0) this.life.decreaseLife();
    else this.setGameOver();
  }
  setGameOver(): void {
    this.clear();
    if (this.info) this.info.innerHTML = 'Game over!';
  }

  drawData(elem: Airplane | Sea | Boat | Parachutist) {
    if (!elem) return;

    this.context?.drawImage(
      elem.image,
      elem.pos.x,
      elem.pos.y,
      elem.getWidth,
      elem.getHeight,
    );
  }
  gameLoop() {
    this.clear();
    this.drawData(this.airplane);
    if (this.airplane.pos.x + this.airplane.getWidth > 0)
      this.airplane.moveAirplane();
    else this.airplane.pos.x = 1080;

    this.drawData(this.boat);
    if (
      (this.boat.isMovingLeft && this.boat.pos.x > 0) ||
      (this.boat.isMovingRight &&
        this.boat.pos.x < this.canvas.width - this.boat.getWidth)
    )
      this.boat.moveBoat();

    // console.log(this.parachutistList.size);
    for (const p of this.parachutistList) {
      //   console.log(p.pos.y <= this.boat.pos.y);
      if (
        p.pos.y > this.boat.pos.y + 10 &&
        p.pos.x > this.boat.pos.x + this.boat.getWidth
      ) {
        this.parachutistList.delete(p);
        this.score.addScore();
      } else if (
        p.pos.y > this.boat.pos.y + 10 &&
        p.pos.x <= this.boat.pos.x + this.boat.getWidth
      ) {
        this.parachutistList.delete(p);
        if (this.life.getLifeScore() == 1) {
          this.setGameOver();
          this.life.decreaseLife();
          return;
        }
        this.life.decreaseLife();
      } else {
        p.moveparachutist();
        this.drawData(p);
      }
    }

    this.drawData(this.sea);
    this.handleParachutist();
    requestAnimationFrame(() => this.gameLoop());
  }
  private getRandom(): boolean {
    return Math.floor(Math.random() * 500 + 1) > 495 ? true : false;
  }
  private handleParachutist(): void {
    if (this.getRandom() ) {
      const parachtistVector: Vector = {
        x: this.airplane.pos.x,
        y: this.airplane.pos.y - this.airplane.getHeight,
      };
      this.parachutistList.add(
        new Parachutist(50, 100, parachtistVector, 3, PARACHTIST_IMG),
      );
    }
  }
}
